@import url('https://fonts.googleapis.com/css2?family=Glory:wght@500;600;700&display=swap');

*{
    font-family: 'Glory', sans-serif;
    box-sizing: border-box;
    border:none; outline:none;
    margin:0; padding:0;
}   

html,body{
    background-color: rgb(213, 255, 188);
}

.keret{
    width:100%;
    height:100vh;
    padding:0px 20px;
}
.keret h1 span{
    display: flex;
    justify-content: right;
    color:rgb(88, 161, 45)
}

.keret .title{
    font-size:3rem;
    margin-bottom:30px;
}

.kiadas{
    border:2px dotted rgb(88, 161, 45);
    border-radius:10px;
    padding:10px 20px;
    font-size:1.8rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom:40px;
}
.kiadas .name{
    color:black;
    font-weight: bold;
}
.kiadas input, button{
    width:100%;
    font-size: 1.4rem;
    border-radius: 5px;
    border:1px solid grey;
    padding:5px;
    margin:5px 0px;
}

.kiadas button{
    background:black;
    border:none;
    color:white;
    letter-spacing: 3px;
    cursor: pointer;
}

.kiadasLista .name{
    font-size:1.8rem;
    font-weight: bold;
}
.kiadasLista .kiadasok .kiadasSingle{
    display: flex;
    flex-direction: column;
    padding:10px;
    font-size:1.3rem;
    margin-top:10px;
    background:white;
    border-radius: 5px;
    box-shadow: 0px 2px 3px rgb(0,0,0,.3);
}
.kiadasLista .kiadasok .kiadasSingle .datum{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    font-style:italic;
}
.kiadasLista .kiadasok .kiadasSingle div{
    display: flex;
    justify-content: space-around;
    margin-bottom:15px;
}
.kiadasLista .kiadasok .kiadasSingle div i{
    background-color: crimson;
    font-size:1.3rem;
    padding:10px;
    border-radius:10px;
    cursor: pointer;
    color:white;
}
.names{
    display: flex;
    justify-content: space-between;
    font-size: 1.8rem;
    color:gray;
}
.names span{
    cursor: pointer;
}
.names .active{
    color:rgb(88, 161, 45);
}
.adatok{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding:10px;
    height:300px;
    font-size:1.5rem;
    margin-top:10px;
    background:rgba(255, 255, 255, 0.397);
    border-radius: 10px;
    box-shadow: 0px 2px 3px rgb(0,0,0,.3);
}
.adatok div{
    display: flex;
    justify-content: space-between;
}
.adatok div .name{
    font-weight:bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size:1.1rem;
    max-width: 40%;
}
.adatok div .price{
    color:crimson;
    font-weight:bold;
}
.kiadasok h3{
    margin-top:10px;
    text-align: center;
    cursor: pointer;
}
.lista-h2{
    margin-bottom:40px;
    display: flex;
    justify-content: space-between;
}
.biztos{
    position:absolute;
    top: 50%;
    border:5px dotted black;
    border-radius:10px;
    background:white;
    width:90%;
    height:40%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.biztos i{
    display: flex;
    justify-content: flex-end;
    font-size:2rem;
    cursor:pointer;
}
.biztos span{
    padding:20px 10px;
}
.biztos div{
    display: flex;
    justify-content: space-between;
}
.biztos div button{
    font-size:1.2rem;
    width:30%;
    margin:10px;
    background:black;
    color:white;
    cursor:pointer;
}

.osszes-link{
    text-decoration: none;
    display: flex;
    justify-content: center;
    color:black;
    font-size:1.2rem;
    margin:10px 0px;
    cursor: pointer;
}